import React, { useState } from 'react';
import SearchBar from './SearchBar';
import ytb from '../apis/ytb'
import VideoList from './VideoList';
import VideoDetail from './VideoDetail';

const App = () => {
    const [videos, updateVideos] = useState([]);
    const [selectedVideo, updateSelectedVideo] = useState(null);

    const onFormSubmit = async (inputValue) => {
        const res = await ytb.get('/search', {
            params: {
                q: inputValue
            }
        });
        updateVideos(res.data.items);
    }

    const onVideoSelected = (video) => {
        updateSelectedVideo(video);
    }

    return (
        <div className="ui container">
            <SearchBar onFormSubmit={onFormSubmit}></SearchBar>
            <div className="ui celled grid">
                <div className="ui row">
                    {selectedVideo ? <div className="ten wide column">
                        <VideoDetail video={selectedVideo}></VideoDetail>
                    </div> : null}

                    <div className={selectedVideo?"six wide column":"sixteen wide column"}>
                        <VideoList onVideoSelected={onVideoSelected} videos={videos}></VideoList>
                </div>
            </div>
        </div>

        </div >
    )
}
export default App;