import React, { useState } from 'react';

function VideoItem(props) {
    const video = props.video;
    const [isMouse, changeMouse] = useState(false);

    const mouseOver = () =>{
        changeMouse(true);
    }
    const mouseOut = () =>{
        changeMouse(false);
    }

    return (
        <div
            className="item" 
            style={isMouse?{backgroundColor:"#FFB900"}:null}
            onMouseOver={()=>mouseOver()} 
            onMouseLeave={()=>mouseOut()}
            onClick={() => props.onVideoSelected(props.video)}
        >
            <div className="ui grid">
                <div className="six wide column">
                    <img alt={video.snippet.title} className="ui image" src={video.snippet.thumbnails.default.url}></img>
                </div>
                <div className="ten wide column" style={{ marginLeft: "-14px", alignItems: "center" }}>
                    <a className="header">{video.snippet.title}</a>
                </div>
            </div>
        </div>
    )
}
export default VideoItem;